
html, body {
    margin: 0;

    padding: 0;
}

body {
    will-change: scroll-position;
}

* {
    box-sizing: border-box;
}

*:after, *:before {
    box-sizing: inherit;
}

:root {
    --P_PageSection__dragged_height: 40px;
    --P_PageSection_sorting-transition-duration: 0.35s;
    --P_PageSection_sorting-transition-easing: ease-out;

    --P_PageSection--placeholder_text: "Umístit sekci zde...";
    --P_PageSection--empty_text: "Sem můžete přidat další sekci...";
}

.E .P_sortable-sections {
    position: relative;

    min-height: 40px;
}

.E .P_sortable-sections:after {
    content: var(--P_PageSection--empty_text);

    font-size: 17px;

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    color: #888;
    opacity: 0;
    visibility: hidden;

    display: flex;
    justify-content: center;
    align-items: center;

    transition: all 0.1s ease-in 0s;
}

.E .P_sortable-sections:empty:after {
    opacity: 1;
    visibility: visible;

    transition: all 0.35s ease-out 0s;
}

.P_PageSection--placeholder {
    position: relative;

    min-height: 0px;
    height: var(--P_PageSection__dragged_height);
    width: 100%;

    color: #888;
    background-color: #fff;
    outline: 2px dashed currentColor;
    outline-offset: -4px;

    transition-property: height, min-height;
    transition-duration: 0.1s;
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);

    will-change: height;
    overflow: hidden;
}

.P_PageSection--placeholder:after {
    content: var(--P_PageSection--placeholder_text);

    font-size: 17px;

    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.P_PageSection--placeholder__transitions {
    height: var(--P_PageSection__dragged_height);
    width: 100%;

    background-color: #fff;

    transition-property: height;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);

    will-change: height;
}


.E_GlobalPageSettings {
    z-index: 110;
    position: fixed;
    bottom: calc(-100% + 20px);
    left: 0px;

    padding: 20px;
    width: 300px;
    height: 100%;

    background-color: #ddd;

    transition: all 0.3s ease-in-out 0s;
}

.E_GlobalPageSettings:hover {
    bottom: 0%;
}

.E_NewPageSectionSelector {
    z-index: 110;
    position: fixed;
    top: calc(-100% + 20px);
    left: 0px;

    padding: 20px;
    width: 300px;
    height: 100%;

    background-color: #ddd;

    transition: all 0.3s ease-in-out 0s;
}

[class*='E_NewPageSection'] {
    will-change: transform, opacity, width, height;
}

.E_NewPageSectionSelector:hover {
    top: 0%;
}

.E_NewPageSectionSelector--page-section-types {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
}

.E_NewPageSectionSelector--page-section-type {
    font-size: 23px;

    margin-bottom: 20px;

	width: calc(50% - 10px);
    height: 105px;

    color: #fff;
    background-color: #404040;

    display: flex;
    justify-content: center;
    align-items: center;
}

.E_NewPageSectionSelector--clone {
    z-index: 120;
}

.P_PageSection {
    position: relative;

    width: 100%;

    transition-property: height;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.P_PageSection--section {
    position: relative;    
}

.P_PageSection__dragged,
.P_PageSection__removed,
.P_PageSection__placed {
    z-index: 100;

    overflow: hidden;
}

.P_PageSection__removed {
    transition: none;
}

.P_PageSection__dragged {
    height: var(--P_PageSection__dragged_height);
    min-height: var(--P_PageSection__dragged_height);
}

.E [class*='P_PageSection'], .E [class*='P_PageSection']:after {
    will-change: transform, opacity, width, height, margin, margin-bottom;
}

.E .P_PageSection:after {
    content: attr(data-page-section-name);

    font-size: 19px;

    z-index: 100;
    position: absolute;
    top: 0px;
    left: 0px;

    width: 100%;
    height: 100%;

    color: #fff;
    background-color: #404040;
    opacity: 0;
    visibility: hidden;

    display: flex;
    justify-content: center;
    align-items: center;

    transition-property: opacity, visibility;
    transition-duration: var(--P_PageSection_sorting-transition-duration);
    transition-timing-function: var(--P_PageSection_sorting-transition-easing);
}

.E .P_PageSection__dragged:after {
    opacity: 1;
    visibility: visible;
}

.P_font-type-1 .P_PageSection--content {
    font-family: serif;
}

.P_font-type-2 .P_PageSection--content {
    font-family: sans-serif;
}
